<template>
    <div>
        <label v-if="label" :class="{'zm-form-item-label-required': isRequired}">{{label}}</label>
        <div>
            <slot></slot>
            <div v-if="validateState === 'error'" class="zm-form-item-message">{{validateMessage}}</div>
        </div>
    </div>
</template>
<script>
    import Emitter from '../../../mixins/emitter';
    import AsyncValidator from "async-validator";

    export default {
        name: 'ZmFormItem',
        mixins: [Emitter],
        inject: ['form'],
        props: {
            label: {
                type: String,
                default: '',
            },
            prop: {
                type: String
            }
        },
        data() {
            return {
                isRequired: false,
                validateState: '', // 
                validateMessage: '', // 
            }
        },
        computed: {
            // formmodel
            fieldValue() {
                return this.form.model[this.prop];
            }
        },
        // Form
        mounted() {
            // Prop
            if (this.prop) {
                this.dispatch('ZmForm', 'on-form-item-add', this);
                // 
                this.initialValue = this.fieldValue;

                this.setRules();
            }
        },
        methods: {
            setRules() {
                let rules = this.getRules();
                if (rules.length) {
                    rules.every(rule => {
                        this.isRequired = rule.required;
                    })
                }
                this.$on("on-form-blur", this.onFieldBlur);
                this.$on("on-form-change", this.onFieldChange);
            },
            // FormrulesFormItem
            getRules() {
                let formRules = this.form.rules;
                formRules = formRules?formRules[this.prop]:[];
                return [].concat(formRules || []);
            },
            // 
            resetField() {
                this.validateState = '';
                this.validateMessage = '';

                this.form.model[this.prop] = this.initialValue;
            },
            // blurchangerules
            getFilteredRule(trigger) {
                const rules = this.getRules();
                return rules.filter(rule => !rule.trigger || rule.trigger.indexOf(trigger) !== -1);
            },
            validate(trigger, callback = function() {}) {
                let rules = this.getFilteredRule(trigger);

                if (!rules || rules.length === 0) {
                    return true;
                }

                // 
                this.validateState = 'validating';

                // async-validator
                let descriptor = {};
                descriptor[this.prop] = rules;

                const validator = new AsyncValidator(descriptor);
                let model = {};

                model[this.prop] = this.fieldValue;
                validator.validate(model, {firstFields: true}, errors => {
                    this.validateState = !errors ? 'success': 'error';
                    this.validateMessage = errors ? errors[0].message: '';

                    callback(this.validateMessage);
                })
            },
            onFieldBlur() {
                this.validate('blur');
            },
            onFieldChange() {
                this.validate('change');
            }
        },
        // Form
        beforeDestroy() {
            this.dispatch('ZmForm', 'on-form-item-remove', this);
        }
    }
</script>